.form-landing-footer-container {
  background-image: url("../../../assets/images/FormLanding/Footer.svg");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: top;
  position: relative;
  z-index: 1; }
  .form-landing-footer-container.sticky-padding {
    padding-bottom: 100px; }
  .form-landing-footer-container .small-container h3 {
    margin-bottom: 10px; }
  .form-landing-footer-container .small-container h6 {
    line-height: normal !important; }
  .form-landing-footer-container .footer {
    max-width: 1110px;
    padding: 120px 40px 40px 40px;
    margin: auto;
    display: flex;
    align-items: center; }
    @media screen and (max-width: 576px) {
      .form-landing-footer-container .footer {
        padding: 120px 15px 40px 15px; } }
    .form-landing-footer-container .footer h3,
    .form-landing-footer-container .footer h6 {
      color: white;
      font-weight: normal; }
    .form-landing-footer-container .footer h5 {
      color: white;
      font-weight: bold;
      font-size: 16px; }
      @media screen and (max-width: 767px) {
        .form-landing-footer-container .footer h5 {
          margin-top: 20px;
          padding-left: 0px !important;
          text-align: left; } }
    .form-landing-footer-container .footer h6 {
      line-height: 11px; }
    .form-landing-footer-container .footer .content-label {
      color: white;
      font-weight: 600;
      font-size: 14px; }
      @media screen and (max-width: 767px) {
        .form-landing-footer-container .footer .content-label {
          text-align: left; } }
  .form-landing-footer-container .big-footer {
    padding: 240px 15px 40px 15px; }
    .form-landing-footer-container .big-footer.full {
      padding-top: 280px; }
    @media screen and (min-width: 576px) {
      .form-landing-footer-container .big-footer.full {
        padding-top: 380px; } }
    @media screen and (min-width: 768px) {
      .form-landing-footer-container .big-footer {
        padding: 240px 40px 40px 40px; }
        .form-landing-footer-container .big-footer.full {
          padding: 280px 80px 80px 80px; } }
  .form-landing-footer-container .pre-line {
    white-space: pre-line; }
  .form-landing-footer-container .contact-info {
    max-width: 200px; }
  .form-landing-footer-container .social-media-group {
    margin-left: 10px;
    margin-top: 25px;
    margin-bottom: 20px;
    max-width: 180px;
    display: flex;
    justify-content: space-between;
    flex-direction: row; }
    .form-landing-footer-container .social-media-group .social-media-icons {
      width: 30px;
      height: 30px; }
  .form-landing-footer-container .facebook-like {
    margin-left: 10px; }
  .form-landing-footer-container .download-now {
    margin-top: 20px;
    padding: 0px 10px; }
  .form-landing-footer-container .download-links {
    flex-wrap: nowrap;
    justify-content: space-between; }
  .form-landing-footer-container .download-img {
    width: 85%;
    height: auto; }
  .form-landing-footer-container .col-display {
    align-self: flex-start;
    display: flex;
    flex-direction: column;
    align-items: flex-start; }
    .form-landing-footer-container .col-display .content-item {
      padding-left: 0px; }
  .form-landing-footer-container .copyright {
    display: flex;
    justify-content: center;
    padding: 30px 0px !important; }
    .form-landing-footer-container .copyright h6 {
      font-size: 16px;
      font-weight: bold; }
  .form-landing-footer-container h5.products-header {
    padding-left: 15px !important; }
  .form-landing-footer-container .copyright-mobile {
    margin-top: 20px;
    padding-left: 10px; }
    .form-landing-footer-container .copyright-mobile h6 {
      font-size: 14px; }
  .form-landing-footer-container .link {
    width: 100%;
    text-align: left; }
  @media screen and (max-width: 767px) {
    .form-landing-footer-container .contact-info,
    .form-landing-footer-container .social-media-group {
      margin-left: auto;
      margin-right: auto; }
    .form-landing-footer-container .facebook-like {
      margin-left: 0px;
      justify-content: center; }
    .form-landing-footer-container .download-now {
      margin-top: 0px; } }
  @media screen and (min-width: 768px) {
    .form-landing-footer-container .copyright {
      justify-content: flex-end; }
    .form-landing-footer-container .col-display {
      flex-direction: row;
      align-items: flex-start; } }

.form-landing-footer-policypal {
  position: absolute;
  left: 0;
  bottom: 5%; }
  .form-landing-footer-policypal img {
    transform: translateY(calc(-50px + (1 * 1vw))); }
    @media screen and (max-width: 1200px) {
      .form-landing-footer-policypal img {
        transform: translateY(calc(-120px + (5 * 1vw))); } }
    @media screen and (max-width: 767px) {
      .form-landing-footer-policypal img {
        transform: translateY(calc(-200px + (15 * 1vw))); } }
    @media screen and (max-width: 600px) {
      .form-landing-footer-policypal img {
        transform: translateY(calc(-180px + (20 * 1vw))); } }
    @media screen and (max-width: 500px) {
      .form-landing-footer-policypal img {
        transform: translateY(calc(-160px + (25 * 1vw))); } }
  .form-landing-footer-policypal .big-footer {
    transform: translateY(calc(-200px + (1 * 1vw))); }
    @media screen and (max-width: 1200px) {
      .form-landing-footer-policypal .big-footer {
        transform: translateY(calc(-200px + (5 * 1vw))); } }
    @media screen and (max-width: 767px) {
      .form-landing-footer-policypal .big-footer {
        transform: translateY(calc(-200px + (15 * 1vw))); } }
    @media screen and (max-width: 500px) {
      .form-landing-footer-policypal .big-footer {
        transform: translateY(calc(-200px + (25 * 1vw))); } }
  .form-landing-footer-policypal img {
    width: 100vw;
    height: auto; }

.form-landing-footer-policypal-full {
  position: absolute;
  top: -50%;
  left: 0;
  right: 0; }
  .form-landing-footer-policypal-full img {
    width: 100%;
    height: auto;
    padding-left: 0 !important;
    padding-right: 0 !important;
    filter: brightness(0) saturate(100%) invert(52%) sepia(31%) saturate(5084%) hue-rotate(208deg) brightness(98%) contrast(88%); }
  @media screen and (max-width: 767) {
    .form-landing-footer-policypal-full {
      top: 0; } }
